import React, { useState } from "react";
import DataTable from "react-data-table-component";
import { useQuery } from "@apollo/client";
import { LISTAR_ORDENES } from "../graphql/queries";
import { Button } from "reactstrap";
import FormatoOrden from "./FormatoOrden";
import { Link } from "react-router-dom";
export default function Reportes() {
  const [enviarId,setEnviarId]= useState();
  const { data } = useQuery(LISTAR_ORDENES, {
    fetchPolicy: "no-cache",
  });
  let datos = data?.listarOrden;

  const cambiarId = (datos) => {
    setEnviarId(datos);
  };

  const columnas = [
    {
      name: "ID",
      selector: "ordensId",
      sortable: true,
    },
    {
      name: "Orden",
      selector: "orden_trabajo",
      sortable: true,
    },
    {
      name: "Estado",
      selector: "estado",
      sortable: true,
      cell: (row) => <h5>{row.estado === true ? "activo" : "inactivo"}</h5>,
    },
    {
      name: "Contrato",
      selector: "contrato",
      sortable: true,
    },
    {
      name: "Municipio",
      selector: "municipio",
      sortable: true,
    },
    {
      name: "Requerimientos",
      selector: "requerimientos",
      sortable: true,
    },
    {
      name: "Action",
      button: true,
      sortable: false,
      // crear el componente y pasarle a la propiedad los datos
      cell: (row) => (
        <Button
         
          color="primary"
        >
          <Link to={`/formato_orden/${row._id}`} style={{textDecoration: 'none', color: 'white'}}>VER</Link>
        </Button>
      ),
    },
  ];

  return (
    <div>
      <div className="container mt-4 ">
        <DataTable
          columns={columnas}
          data={datos}
          title="Ordenes de Trabajo"
          pagination
        />
        
        
      </div>
    </div>
  );
}
